html {
	@include typography;
}

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

// Fallback for when there is no custom background color defined.
body {
	background: var(--color-white);
}

p {
	margin: 0 0 $global-spacing-unit-4;
}



